import { HttpClient } from '@angular/common/http';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ChartDataSets } from 'chart.js';
import { Observable } from 'rxjs';
import { genArray, genNumber, genString, IUser } from 'src/app/utils';

type ArrayItem = number | string | IUser;
const jsonPath = 'assets/';
interface IOperation {
  name: string,
  fn: () => any,
  isFaster: number,
  averageTime: number,
  results: number[],
  class?: string
}

export interface IResultItem {
  operation1: IOperation,
  operation2: IOperation,
  isEqual: number,
  Failes?: number,
}

export interface IResult {
  label: string,
  index: number,
  value: IResultItem
}
export interface IServerResult {
  label: string,
  data:IResult
}

enum DataType {
  Number,
  String,
  Object
};

@Component({
  selector: 'app-array-test',
  templateUrl: './array-test.component.html',
  styleUrls: ['./array-test.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ArrayTestComponent implements OnInit {
  iterationsCount = 1000000;
  serverResult: IServerResult[] = [];
  showServerResult = false;
  arraySize = 100;
  experiments = 10;
  maxExperimentsCount = 8000;
  numbers: number[] = [];
  strings: string[] = [];
  objects: IUser[] = [];
  dataType: DataType = DataType.Number;
  data: { numbers: number[], strings: string[], objects: Object[] };
  processing = false;
  result: IResult[] = [];
  sampleIndexes = [1, Math.ceil(this.arraySize / 2), this.arraySize - 1];

  constructor(private http: HttpClient, private cdr: ChangeDetectorRef) {
    this.getJSON(`${jsonPath}data.json`).subscribe(data => {
      this.data = data
    });
    this.getJSON(`${jsonPath}result.json`).subscribe(data => {
      this.serverResult = data;
      this.cdr.markForCheck();
    });
  }

  ngOnInit(): void {
    //this.numbers = genArray(genNumber, arraySize);
    //this.strings = genArray(genString, arraySize);
    //this.objects = this.genArray(this.genObject.bind(this));

    //this.runExperiment(this.numbers);
  }

  handleClick(): void {
    if (this.processing) {
      return;
    }

    this.processing = true;
    this.sampleIndexes = this.genSampleIndexes(this.arraySize);
    console.log('indexes: ', this.sampleIndexes);
    let arr = [];
    this.result = [];
    if (this.dataType === DataType.Number)
      arr = this.data.numbers.slice(0, this.arraySize)
    else if (this.dataType === DataType.String)
      arr = this.data.strings.slice(0, this.arraySize)
    else if (this.dataType === DataType.Object)
      arr = this.data.objects.slice(0, this.arraySize)

    setTimeout(async () => {
      this.result = await this.runExperiment(arr);
      this.processing = false;
      this.cdr.markForCheck();
    }, 50);
  }



  onExperimentsChange(e: Event): void {
    const count = +(e.target as HTMLInputElement).value;
    if (count > this.maxExperimentsCount) {
      this.experiments = this.maxExperimentsCount;
      return;
    }
    this.experiments = count;
  }

  onDataTypeChange(type: DataType): void {
    this.dataType = type;
  }

  async runExperiment(arr: ArrayItem[]): Promise<IResult[]> {
    const result = [];
    console.log('data: ', arr)
    for (let i of this.sampleIndexes) {
      const searchEl = arr[i];
      const fn2: IOperation = {
        name: 'findIndex',
        fn: () => arr.findIndex(x => x === searchEl),
        results: [],
        averageTime: 0,
        isFaster: 0
      };
      const fn1: IOperation = {
        name: 'indexOf',
        fn: () => arr.indexOf(searchEl),
        results: [],
        averageTime: 0,
        isFaster: 0
      };
      const value = await this.compare(fn1, fn2);
      result.push({
        label: `Index of searching element: ${i}`,
        value,
        index: i
      });
    }
    return Promise.resolve(result)

  }

  async calcTime(fn: () => any, iterations: number = this.iterationsCount): Promise<number> {
    performance.mark('a');
    for (let i = 0; i < iterations; i++) {
      fn();
    };
    performance.mark('b');
    performance.mark('c');
    for (let i = 0; i < iterations; i++) { };
    performance.mark('d');
    performance.measure("ab", 'a', 'b');
    performance.measure("cd", 'c', 'd');
    const value = performance.getEntriesByName("ab","measure")[0]?.duration;
    const cycle = performance.getEntriesByName("cd","measure")[0]?.duration;

    performance.clearMarks();
    performance.clearMeasures();
    const diff = ((value as number) - (cycle as number)) / iterations;
    await Promise.resolve(1);
    return diff;
  }

  async compare(operation1: IOperation, operation2: IOperation, count: number = this.experiments): Promise<IResultItem> {
    const result: IResultItem = {
      operation1,
      operation2,
      isEqual: 0,
      Failes: 0
    };

    result.operation1.isFaster = result.operation2.isFaster = 0;

    for (let i = 0; i < count; i++) {
      const res1 = await this.calcTime(operation1.fn);
      await new Promise(resolve => setTimeout(resolve, 1));
      const res2 = await this.calcTime(operation2.fn);
      if (!i) {
        result.operation1.averageTime = res1;
        result.operation2.averageTime = res2;
      } else {
        result.operation1.averageTime = (result.operation1.averageTime + res1) / 2;
        result.operation2.averageTime = (result.operation2.averageTime + res2) / 2;
      }
      result.operation1.class = result.operation2.class = '';
      result.operation1.results.push(res1);
      result.operation2.results.push(res2);

      if (res1 && res2) {
        if (res1 < res2)
          result.operation1.isFaster++
        else if (res1 > res2)
          result.operation2.isFaster++;
        else
          result.isEqual++;

        continue;
      }
      else result.Failes++;

    }
    return result;
  }

  private genSampleIndexes(dataLength: number, maxSamplesLength = 20): number[] {
    const arr = [];
    if (!dataLength) {
      return arr;
    }

    let length = dataLength;
    if (dataLength > maxSamplesLength) {
      length = maxSamplesLength;
    }

    let offset = Math.trunc(dataLength / length);

    for (let i = 0; i < dataLength; i = i + offset) {
      arr.push(i);
    }

    return arr;
  }

  public getJSON(url: string): Observable<any> {
    return this.http.get(url);
  }


}